import React, { Component } from 'react';
import './App.css';

class App extends Component {
  constructor(props) {
    super(props);
    this.state = { 
      categoryCode: '',
      contentTypeCode: '', 
      widgetTitle: ''
    };
  }

  handleChange(target) {
    this.setState({
      [target.name]: target.value
    });
  }

  render() {
    const { categoryCode, 
            contentTypeCode,
            widgetTitle } = this.state;
    return (
      <div>
        <h1>
          Select the news category and content type
        </h1>
        {/* eslint-disable-next-line jsx-a11y/label-has-associated-control */}
        <label htmlFor="widgetTitle">Widget Title </label>
        <input type="text" name="widgetTitle" 
                value={widgetTitle} 
                onChange={e => this.handleChange(e.target)} />
        <br/>
        <label htmlFor="categoryCode">Category </label>
        <select
          name="categoryCode"
          value={categoryCode}
          onChange={e => this.handleChange(e.target)}
        >
          <option value="">Select a value</option>
          <option value="industry_news">Industry News</option>
          <option value="client_news">Client News</option>
        </select>
        <br/>
        <label htmlFor="contentTypeCode">Content Type </label>
        <select
          name="contentTypeCode"
          value={contentTypeCode}
          onChange={e => this.handleChange(e.target)}
        >
          <option value="">Select a value</option>
          <option value="nws">News</option>
        </select>
      </div>
    );
  }
}

export default App;
